.bunner {
    position: relative;
    // display: flex;
    align-items: center;
}

video {
    object-fit: cover;
    width: 100%;
    height: 45vw;
    // position: relative;
    top: 0;
    left: 0;
  }
  
.bunner-wrapper {
    margin: 0 auto;
    width: 30vw;
    text-align: center;
}


  
  .bunner-viewport {
    position: absolute;
    top: 35%;
    left: 0;
   
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .bunner-text {
    // color: black;
    color: wheat;
    text-transform: uppercase;
    font-size: 3.5vw;
    text-align: center;
    width: 80%;
    font-weight: 500;
  }
  
  .icon-scroll,
  .icon-scroll::before {
      position: absolute;
      left: 50%;
      top: 240%;
      transform: translateX(-50%);
  }
  
  .icon-scroll {
      width: 30px;
      height: 60px;
      border: 2px solid #fff;
      border-radius: 25px;
      cursor: pointer;
      bottom: 2rem;
  }
  
  .icon-scroll::before {
      content: '';
      width: 8px;
      height: 8px;
      background: #fff;
      margin-left: -4px;
      top: 15px;
      border-radius: 50%;
      animation: 1.5s scroll cubic-bezier(0.65, -0.55, 0.25, 1.5) infinite;
  }
  
  @keyframes scroll {
      0% {
          opacity: 0;
          transform: translateY(0) scale(0.5);
      }
      10% {
          opacity: 1;
          transform: scale(1);
      }
      100% {
          opacity: 0;
          transform: translateY(30px) scale(0.5);
      }
  }