@media (min-width: 1200px) {
    .container {
        width: 1100px!important;
    }
}


@media (max-width: 1060px) {

    .company__cards-item {
        width: 40%!important;
        height: 19vw;
    }

    .cards-item__title {
        font-size: 2.5vw!important;
    }

    .company__title {
        font-size: 4vw!important;
    }

    .posns__info-title {
        font-size: 18px!important;
    }

    .contacts-margin {
        margin-right: 0!important;
    }

    .icon-scroll {
        display: none;
    }
    
}




@media (max-width: 850px) {
    video {
        height: 95vw!important;
    }

    .icon-scroll, .icon-scroll::before {
        top: 464%!important;
    }

    .contacts-item__link {
        font-size: 19px!important;
    }

    .header__downloads {
        font-size: 19px!important;
    }

    .bunner-text {
        color: wheat;
        text-transform: uppercase;
        font-size: 6.5vw!important;
        text-align: center;
        width: 102%!important;
        font-weight: 500;
    }

    #mainnav #menu {
        display: block!important;
    }
    
    .header__nav {
        display: none!important;
    }

    .posns__item {
        width: 35%!important;
    }

    .member__img img {
        width: 99%!important;
    }

    .team__member {
        width: 32%!important;
    }

}



@media (max-width: 750px) {

    .team__member {
        width: 74% !important;
    }

    .contacts__item {
        width: 38%!important;
    }


    .contacts__item-link {
        font-size: 20px!important;
    }

    .company__title {
        font-size: 6vw !important;
    }

    .team__member .member__cont h3 {
        font-size: 26px!important;
    }

    .team__member .member__cont span {
        font-size: 19px!important;
    }

    .input-wrapper {
        flex-direction: column!important;
        width: 100%!important;
    }

    .header-top__contacts {
        flex-direction: column!important;
    }

    .header-top-wrapper {
        flex-direction: column!important;
    }

    .header__downloads {
        margin-left: 0!important;
    }

    .contacts-form .input-wrapper input {
        width: 100%!important;
    }


    .main-btn {
        width: 100%!important;
    }

}





@media (max-width: 650px) {

    .team__member {
        width: 74% !important;
    }

    .company__cards-item {
        width: 83% !important;
        height: 19vw!important;
    }





}