.company {
    display: flex;
    margin-top: 5vw;
    margin-bottom: 6vw;
}

.company-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.company__title {
    text-align: center;
    font-size: 3vw;
    &:after {
        content: "";
        display: block;
        height: 4px;
        width: 60px;
        background-color: #FFD600;;
        // margin-top: 20px;
    }
}

.company-wrapper .company__title:after {
    margin: 20px auto 0;
}

.company__cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.company__cards-item {
    display: flex;
    flex-direction: row;
    padding: 40px 20px 32px 18px;
    margin: 15px 0;
    border: 1px solid #eee;
    position: relative;
    width: 27%;
    margin: 20px 10px;
    &:after {
    
      }
}

.company__cards-item {

    position: relative;
    // margin: 50px auto;
  }
  .company__cards-item:before, .company__cards-item:after {
    content: "";
    position: absolute;
    top: 10px;
    left: 10px;
    right: 10px;
    bottom: 10px;
    opacity: 0;
    transition: all 2s ease;
  }
  .company__cards-item:before {
    border-top: 1px solid;
    border-bottom: 1px solid;
    transform: scaleX(0);
  }
  .company__cards-item:after {
    border-left: 1px solid;
    border-right: 1px solid;
    transform: scaleY(0);
  }
  .company__cards-item:hover:before, .company__cards-item:hover:after {
    transform: scale(1);
    opacity: 1;
  }
  



.company__cards-item-img img {
    width: 56px;
    height: 52px;
    z-index: 15;
}

.company__cards-item-text {
    display: flex;
    flex-direction: column;
    margin-left: 20px;
    z-index: 11;
}

.cards-item__title {
    font-size: 1.5vw;
    font-weight: 600;
    margin-bottom: 20px;
}

.cards-item__text {
    font-weight: 500;
    font-size: 16px;
    line-height: 1.6;
    overflow-x: hidden;
    color: #868f9b;
}



