.team {
    display: flex;
    margin: 5vw 0;
}

.team-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.team-wrapper .company__title:after {
    margin: 20px auto 0;
}

.team__members {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    // flex-direction: row;
    align-items: center;
    margin: 2%;
}

.team__member {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #f4f4f4;
    padding: 56px 20px;
    margin: 15px 20px;
    text-align: center;
    width: 25%;
    height: 250px;
}

.member__img {
    margin-bottom: 20px;
}

.team__member .member__cont h3 {
    margin-bottom: 10px;
    -webkit-transition: .2s color;
    transition: .2s color;
    font-weight: 700;
    // margin-top: 0;
    margin-bottom: 20px;
    font-size: 20px;
    color: #10161a;
}

.team__member .member__cont span {
    font-size: 14px;
    text-transform: uppercase;
    -webkit-transition: .2s color;
    transition: .2s color;
    color: #868f9b;
}

