.contacts {
    display: flex;
    padding: 5vw 0;
}

.contacts-wrapper .company__title:after {
    margin: 20px auto 0;
}

.contacts-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.contacts-items {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-top: 3vw; 
    width: 85%;   
}

.contacts-items:nth-child(1) {
    margin: 0 4vw;
}

.contacts__item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 33%;
}
.contacts__item img {
    width: 60px;
    height: 56px;
}


.contacts__item-icon {

}


.contacts__item-title {
    font-weight: 700;
    margin-top: 20px;
    margin-bottom: 20px;
    color: #10161a;
    font-size: 21px;
    

}

.contacts__item-link {
    color: #FFD600;
    text-decoration: none;
    text-align: center;
    line-height: 1.6;
    font-weight: 500;
    &:hover {
        font-size: 16px;
        transition: 1.5s;
        font-weight: 600;
    }
}

.contacts__item--margin {
    margin-top: 23px;
}

.contacts-form {
    width: 80%;
}

.contact-form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.input-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;

}

.contacts-form .input-wrapper input {
   width: 46%;
}


.contact-form {
    display: flex;
    flex-direction: column;
    text-align: center;
    margin-top: 40px;
    justify-content: center;
    align-items: center;
}


.contact-form .input {
    margin-bottom: 20px;
    width: 100%;
}

input[type=text], input[type=email] {
    height: 40px;
    // width: 100%;
    border: none;
    background: #eee;
    border-bottom: 2px solid #eee;
    color: black;
    padding: 0 10px;
    // opacity: .5;
    -webkit-transition: .2s border-color,.2s opacity;
    transition: .2s border-color,.2s opacity;
    box-sizing: border-box;
}

textarea {
    height: 130px;
    box-sizing: border-box;
    // width: 100%;
    border: none;
    background: #eee;
    border-bottom: 2px solid #eee;
    color: #354052;
    padding: 5px 10px;
    // opacity: .5;
    -webkit-transition: .2s border-color,.2s opacity;
    transition: .2s border-color,.2s opacity;
    width: 100%;
}

.main-btn {
    background: #FFD600;
    color: #fff;
    width: 30%;
}

.main-btn {
    padding: 10px 35px;
    margin: 30px 0;
    border: 2px solid transparent;
    border-radius: 3px;
    -webkit-transition: .2s opacity;
    transition: .2s opacity;
    cursor: pointer;
    font-size: 20px;
    // margin-top: 10px;
    &:hover {
        box-shadow: 0 0 20px 0 #FFD600 inset, 0 0 20px 2px #FFD600;
        border: 2px solid #FFD600;
        transition: .2s;
      }
}
.contacts-margin {
    margin-right: 7%;
}