@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Russo+One&display=swap');

body {
    font-family: 'Montserrat', sans-serif;
}

header {
    display: flex;
    flex-direction: column;
}

.header-top-wrapper {
    display: flex;
    flex-direction: row;
    margin: 6px 0;
}

.header-top__contacts {
    display: flex;
    flex-direction: row;
}

.header-top__contacts-item {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-right: 20px;
}

.contacts-item__text {
    line-height: 1.7;
    color: #4d4d4d;
    font-weight: 500;
    font-size: 17px;
    margin-right: 5px;

}

.contacts-item__link {
    font-size: 16px;
    color: #FFD600;
    text-decoration: none;
    font-weight: 4s00;
}



.header__downloads {
    margin-left: auto;
    font-size: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    
}
.header__downloads img {
    width: 23px;
    color: #4d4d4d;
}

.header__downloads-item {
    display: flex;
    justify-content: center;
}

.header__downloads-item a {
    text-decoration: none;
    color: #FFD600;
    display: flex;
    justify-content: center;
    align-items: center;
}
.header__downloads-item span {
    margin-left: 8px;
    font-weight: 500;
    &:hover {
        font-size: 17px;
        transition: 1s;
        font-weight: 600;
    }
}

.top-bar {
    border-bottom: 1px solid #e9ecef;
}


.header-bottom {
    margin: 10px;
}

.nav-name {
    font-size: 30px;
    letter-spacing: 3px;
    // height: 200;
    // font-family: 'Pangolin', cursive;
    // font-family: 'Stalinist One', cursive;
    font-family: 'Russo One', sans-serif;
    
}

.header-bottom__logo img {
    width: 120px;
    height: 50px;
}

.header-bottom-wrapper {
    display: flex;
    flex-direction: row;
    position: relative;
}

.header__nav {
    display: flex;
    flex-direction: row;
    align-items: center;
    // justify-content: center;
    margin-left: auto;
    font-size: 16px;
}
.header__nav-item {
    padding: 5px 20px;
    color: #000;
    display: inline-block;
    text-decoration: none!important;
    cursor: pointer;
    &:hover {
        color: #FFD600;
    }

}


.header-button {
    text-align: center;
    padding: 10px 10px;
    background: #FFD600;
    color: #fff;
    transition: 1.2s opacity;
    border-radius: 3px;
    &:hover {
        color: black;
        transition: 1s;
    }
    
}

#topBtn{
    position: fixed;
    bottom: 40px;
    right: 40px;
    font-size: 22px;
    width: 50px;
    height: 50px;
    background: #FFD600;
    color: white;
    border: none;
    cursor: pointer;
    display: none;
    z-index: 111;
  }

#mainnav {
    background: black;
    color: #fff;
    font-weight: bold;
    text-transform:uppercase;
    letter-spacing: .05em;
    z-index: 555;
}


#mainnav #menu {
    display: none;
    padding: .8em 1.5em;
    cursor: pointer;
}


#mainnav ul {
    display: none;
    margin: 0;
    text-align: center;
}

#mainnav ul li {
    margin: 0;
    list-style: none;
    display: inline-block;
    text-align: center;
    font-size: 24px;
}

#mainnav ul li a {
    color: black;
    text-decoration: none;
    display: inline-block;
    padding: .9em 1.5em .8em 1.5em;
}

#mainnav ul li a:hover {
    color: #000;
    background: #aaa;
}

.group {
    margin-left: auto;
    position: absolute;
    right: 0;
    top: -5px;
}

// #mainnav #menu {
//     display: block;
// }

#mainnav ul {
    display: none;
    background: white;
    padding: 1em;
    text-align: left;
}
#mainnav ul li {
    display: block;
}

#mainnav ul li a {
    display: block;
    padding: .75em 1.5em .5em 1.5em;
}

